import XLSX, { utils } from 'xlsx'

const EXCEL = {
    // Excel
    /**
     * @description                  
     * @param {Object} option        Object--
     * @param option.title      Array--['','','','']
     * @param option.body       Array--:[['',28,'','aa'],['',29,'','aa']]
     * @param option.name       String--:'demo'
     * @param option.suffix     String--'csv'
     * @param option.merges     Array--[
          { s: { r: 0, c: 0 }, e: { r: 1, c: 0 } },-----A1A2
          { s: { r: 0, c: 1 }, e: { r: 0, c: 2 } },-----B1C1
          { s: { r: 0, c: 3 }, e: { r: 0, c: 4 } },-----D1E1
          { s: { r: 0, c: 5 }, e: { r: 1, c: 5 } }-----F1F2
        ]------s:start,e:end,r:row,c:cell;
     */
    exportFromArray(option) {
        const config = Object.assign({}, { name: 'demo', suffix: 'csv' }, option)
        const { title, body, name, suffix, merges } = config;
        const array = [...title, ...body];
        const workBook = utils.book_new();//workBook
        const workSheet = utils.aoa_to_sheet(array);//workSheet
        const fileName = name + '.' + suffix;
        workSheet['!merges'] = merges;
        utils.book_append_sheet(workBook, workSheet, name);
        XLSX.writeFile(workBook, fileName, { bookType: suffix, type: 'buffer' })
    },
    /**
     * @description                      
     * @param {Object} option            
     * @param {string} option.id         domid,
     * @param {string} option.name       String--:'demo'
     * @param {string} option.suffix     String--'csv'
     */
    exportFromTable(option) {
        const config = Object.assign({}, { name: 'demo', suffix: 'csv',sheetName:'' }, option),
            { id, name, suffix, merges,sheetName } = config,
            workBook = utils.book_new(),
            workSheet = utils.table_to_sheet(document.getElementById(id)),
            fileName = name + '.' + suffix;
        workSheet['!merges'] = merges;
        utils.book_append_sheet(workBook, workSheet, sheetName);
        XLSX.writeFile(workBook, fileName, { bookType: suffix, type: 'buffer' })
    },
    /**
     * @description  
     * @param    {Object} file    
     * @returns  {Object} content    
     */
    async importFromLocal(file) {
        const workBook = await this.readerWorkBookFromLocal(file);
        const content = workBook.SheetNames.map(item => {
            return utils.sheet_to_json(workBook.Sheets[item])
        });
        return content;
    },
    /**
     * @description              
     * @param {Object} file      
     */
    readerWorkBookFromLocal(file) {
        const reader = new FileReader();
        reader.readAsBinaryString(file);
        return new Promise(function (resolve, reject) {
            reader.onload = function (e) {
                const fileData = e.target.result;
                if (reader.readyState === 2) {
                    const workBook = XLSX.read(fileData, { type: 'binary' });
                    resolve(workBook);
                } else {
                    reject('');
                }
            }
        })
    }
}

export default EXCEL;


