<template>
  <div>
    <v-spin :spinning="importVisible" tip="...">
      <v-card :title="titleVal">
        <template slot="extra" v-if="isModel">
          <v-button-group size="small">
            <v-button type="primary" @click="handleDownTpl">
              <v-icon type="cloud-download"></v-icon>
            </v-button>
            <v-button type="primary" @click="handleSelectFile">
              <v-icon type="cloud-upload"></v-icon>
            </v-button>
          </v-button-group>
        </template>
        <div class="clearfix margin-bottom-15">
          <div class="fl">
            <v-input-search
              size="small"
              placeholder=""
              style="width: 400px"
              @search="onSearch"
              enterButton
              v-model="searchVal"
            ></v-input-search>
          </div>
          <v-button type="primary" size="small" class="fr" @click="handleAddClick"></v-button>
        </div>
        <v-table
          :columns="columns"
          :dataSource="data"
          :pagination="pagination"
          :loading="loading"
          @change="handleTableChange"
          bordered
        >
          <template slot="apitype" slot-scope="text, record, index">{{apiTypeFun(record.api_type)}}</template>
          <template slot="apistatus" slot-scope="text, record, index">
            <v-tag
              :color="record.api_status == '0' ? 'red' : record.api_status == '1' ? 'green' : record.api_status == '2' ? 'blue' : 'orange'"
            >{{apistatusFun(record.api_status)}}</v-tag>
          </template>
          <template slot="operation" slot-scope="text, record, index">
            <div class="editable-row-operations">
              <v-button-group size="small">
                <v-button @click="() => handleEidt(record)"></v-button>
                <v-button @click="() => handleDelete(record)"></v-button>
                <v-button @click="() => handleCopy(record)"></v-button>
                <v-button @click="() => handleView(record)"></v-button>
              </v-button-group>
            </div>
          </template>
        </v-table>
      </v-card>

      <iframe name="myIframe" style="display:none"></iframe>
      <input type="file" id="file" style="display:none" @change="importFile" />
    </v-spin>
  </div>
</template>

<script>
import Util from "@/libs/util";
import Excels from "@/libs/excel";
const apiTypeFun = type => {
  let _type = "";
  switch (type) {
    case "0":
      _type = "GET";
      break;
    case "1":
      _type = "PUT";
      break;
    case "2":
      _type = "POST";
      break;
    case "3":
      _type = "DELETE";
      break;
    case "4":
      _type = "OPTIONS";
      break;
    case "5":
      _type = "PATCH";
      break;
  }
  return _type;
};

const switchTypeFun = type => {
  let _type = "";
  switch (type) {
    case "GET":
      _type = "0";
      break;
    case "PUT":
      _type = "1";
      break;
    case "POST":
      _type = "2";
      break;
    case "DELETE":
      _type = "3";
      break;
    case "OPTIONS":
      _type = "4";
      break;
    case "PATCH":
      _type = "5";
      break;
  }
  return _type;
};
const apistatusFun = status => {
  let _status = "";
  switch (status) {
    case "0":
      _status = "";
      break;
    case "1":
      _status = "";
      break;
    case "2":
      _status = "";
      break;
    case "3":
      _status = "";
      break;
  }
  return _status;
};

const renderFileArr = (data, pid, mid) => {
  let _data = [];
  if (data[0].length == 0) return "1";
  if (data[1].length == 0) return "2";
  let isapiurl = false;
  data[0].forEach(itemone => {
    const reg = /^(?!\/)/;
    isapiurl = reg.test(itemone[""]);
    if (!isapiurl) return;
    let res = {
      projectid: pid,
      moduleid: mid,
      apiname: itemone[""],
      apiurl: itemone[""],
      apitype: switchTypeFun(itemone[""]),
      apidesc: "",
      apistatus: "2",
      apiheaderdesc: "[]",
      apiparmsdesc: "[]",
      ismockjs: 0,
      apireqheader: "{}",
      apireqheaderdesc: "[]",
      apilazytime: 0
    };
    let apicontentdesc = [];
    data[1].forEach(itemtwo => {
      if (itemtwo["ID"] == itemone["ID"]) {
        apicontentdesc.push({
          name: itemtwo[""],
          type: itemtwo[""],
          value: Util.replaceQuotes(itemtwo[""]),
          desc: Util.replaceQuotes(itemtwo[""])
        });
      }
    });

    let apibodydesc = [];
    if (data[2].length) {
      data[2].forEach(itemthr => {
        if (itemthr["ID"] == itemone["ID"]) {
          apibodydesc.push({
            name: itemthr[""],
            type: itemthr[""],
            must: itemthr[""],
            desc: Util.replaceQuotes(itemthr[""])
          });
        }
      });
    }

    if (data[3].length) {
      data[3].forEach(itemfou => {
        if (itemfou["ID"] == itemone["ID"]) {
          if (itemfou[""] != undefined && itemfou[""] != "") {
            res.apicontent = itemfou[""];
          } else {
            res.apicontent = JSON.stringify({
              status: 200,
              title: "ok",
              payload: []
            });
          }
        }
      });
    }

    if (res.apicontent == undefined) {
      res.apicontent = JSON.stringify({
        status: 200,
        title: "ok",
        payload: []
      });
    }

    res.apicontentdesc = JSON.stringify(apicontentdesc);
    res.apibodydesc = JSON.stringify(apibodydesc);
    _data.push(res);
  });
  if (!isapiurl) return "apiurl";
  return _data;
};
export default {
  name: "interface_index",
  data() {
    return {
      importFileAllVal: [],
      importVisible: false,

      isModel: false,
      titleVal: "",
      searchVal: "",
      data: [],
      pagination: { showQuickJumper: true },
      loading: false,
      columns: [
        {
          title: "",
          dataIndex: "mockid",
          width: "5%",
          align: "center"
        },
        {
          title: "",
          dataIndex: "api_name",
          align: "center"
        },
        {
          title: "",
          dataIndex: "api_url",
          align: "center"
        },
        {
          title: "",
          scopedSlots: { customRender: "apitype" },
          dataIndex: "api_type",
          align: "center"
        },
        {
          title: "",
          dataIndex: "projectname",
          align: "center"
        },
        {
          title: "",
          dataIndex: "modulename",
          align: "center"
        },
        {
          title: "",
          scopedSlots: { customRender: "apistatus" },
          dataIndex: "api_status",
          align: "center"
        },
        {
          title: "",
          width: "20%",
          scopedSlots: { customRender: "operation" },
          align: "center"
        }
      ]
    };
  },
  computed: {
    projectid() {
      return this.$route.query.pid;
    },
    moduleid() {
      return this.$route.query.mid;
    }
  },
  watch: {
    $route: {
      handler(route) {
        if (this.moduleid != undefined) {
          this.isModel = true;
          this.getInvolv();
        } else {
          this.isModel = false;
          this.titleVal = "";
        }
        this.fetch(1);
      },
      immediate: true
    }
  },
  methods: {
    apiTypeFun,
    apistatusFun,
    getInvolv() {
      this.$request({
        method: "GET",
        url: `/modules/involv/${this.moduleid}`
      }).then(res => {
        let _res = res.data[0];
        this.titleVal = `${_res.projectname} > ${_res.modulename} > `;
      });
    },
    //   pageNum  
    fetch(pageNum) {
      this.loading = true;
      this.$request({
        method: "GET",
        url: `/interfaces`,
        params: {
          searchval: this.searchVal,
          projectid: this.projectid,
          moduleid: this.moduleid,
          page: pageNum,
          pagesize: 10
        }
      })
        .then(res => {
          this.data = res.data.rows;
          this.pagination = Util.pager(this, this.pagination, {
            current: pageNum,
            total: res.data.totals
          });
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    //
    handleTableChange(p) {
      this.fetch(p.current);
    },
    //
    onSearch() {
      this.fetch(1);
    },
    //
    handleAddClick() {
      let query = {
        pid: this.$route.query.pid,
        mid: this.$route.query.mid
      };
      this.$router.push({
        name: "interface_add",
        query
      });
    },
    //
    handleCopy(record) {
      this.$router.push({
        name: "interface_add",
        query: {
          id: record.mockid,
          iscopy: 1
        }
      });
    },
    //
    handleView(record) {
      this.$router.push({
        name: "interface_postman",
        query: {
          id: record.mockid
        }
      });
    },
    //
    handleEidt(record) {
      this.$router.push({
        name: "interface_edit",
        query: {
          id: record.mockid
        }
      });
    },
    //
    handleDelete(record) {
      let _this = this;
      this.$confirm({
        title: "",
        content: "?",
        onOk() {
          _this
            .$request({
              method: "POST",
              url: "/interfaces/delete",
              data: {
                id: record.mockid
              }
            })
            .then(res => {
              if (res) {
                _this.$message.success(res.message);
                _this.fetch(1);
              }
            });
        }
      });
    },

    async importFile() {
      const el = document.getElementById("file");
      const file = el.files[0];
      const data = await Excels.importFromLocal(file);
      try {
        let arr = renderFileArr(data, this.projectid, this.moduleid);
        if (arr == "1") {
          this.$message.error("");
        } else if (arr == "2") {
          this.$message.error("");
        } else if (arr == "apiurl") {
          this.$message.error("/");
        } else {
          this.submitAddInterface(arr);
        }
      } catch (error) {
        alert(error);
      }
    },
    //
    async submitAddInterface(data) {
      this.importVisible = true;
      let res = await this.$request({
        method: "POST",
        url: "/interfaces/batchAdd",
        data: {
          allInterface: JSON.stringify(data)
        }
      });
      if (res.code == 200) {
        this.$message.success(res.message);
        this.fetch(1);
      } else {
        this.$error({
          title: "",
          content: res.message
        });
      }
      this.importVisible = false;
    },
    //
    handleDownTpl() {
      window.open(`/mockServe_interface_batch_tpl.xls`, "myIframe");
    },
    //excel
    handleSelectFile() {
      const el = document.getElementById("file");
      el.click();
    }
  }
};
</script>
<style lang="less" scoped>
</style>


